// Backend configuration - can be customized by users
export interface BackendConfig {
  serverUrl?: string;
  serverSocket?: string;
  clientSocket?: string;
  clientUrl?: string;
  secretKey?: string;
  backendUrl?: string;
  assetBaseUrl?: string; // Base URL for asset files (images, SVGs, etc.)
}

// Default configuration
const defaultConfig: Required<BackendConfig> = {
  serverUrl: "https://wagmirequest.la",
  serverSocket: "wss://api.riveanimation.cards",
  clientSocket: "wss://api.riveanimation.cards",
  clientUrl: "https://www.riveanimation.cards/v1",
  secretKey: "ABCDEF",
  // backendUrl: "http://localhost:3000",
  backendUrl: "https://api.riveanimation.cards",
  assetBaseUrl: "https://www.riveanimation.cards", // Default asset base URL
};

let currentConfig = { ...defaultConfig };

export const getConfig = (): Required<BackendConfig> => ({ ...currentConfig });

export const setConfig = (config: Partial<BackendConfig>): void => {
  currentConfig = { ...currentConfig, ...config };
};

export const getServerUrl = (): string => currentConfig.serverUrl;
export const getServerSocket = (): string => currentConfig.serverSocket;
export const getClientSocket = (): string => currentConfig.clientSocket;
export const getClientUrl = (): string => currentConfig.clientUrl;
export const getSecretKey = (): string => currentConfig.secretKey;
export const getBackendUrl = (): string => currentConfig.backendUrl;
export const getAssetBaseUrl = (): string => currentConfig.assetBaseUrl;

