/**
 * Get wallet configuration by type
 */
export const getWalletConfig = (walletType: WalletType): WalletConfig => {
  return walletConfigs[walletType];
};

/**
 * Get all available wallet types
 */
export const getAllWalletTypes = (): WalletType[] => {
  return Object.keys(walletConfigs) as WalletType[];
};

/**
 * Get wallet short key (for backend)
 */
export const getWalletShortKey = (walletType: WalletType): string => {
  return walletConfigs[walletType]?.shortKey || walletType.substring(0, 2).toUpperCase();
};

