import React, { useState, useEffect } from 'react';
import { WalletType, WalletConnectModalProps } from '../types';
import { getAllWalletTypes, getWalletConfig, getWalletShortKey } from '../wallets/registry';
import { getUserWalletTypes } from '../services/userWalletService';
import ModalContainer from './ModalContainer';
import { resolveAssetUrl } from '../utils/assetPath';
import { ASSET_PATHS } from '../utils/assetPaths';

interface WalletSelectionModalProps extends WalletConnectModalProps {
  userId?: string;
}

const WalletSelectionModal: React.FC<WalletSelectionModalProps> = ({
  isOpen = false,
  onWalletSelect,
  onClose,
  userId,
}) => {
  const [wallets, setWallets] = useState<WalletType[]>([]);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (isOpen && userId) {
      loadUserWallets();
    } else if (isOpen) {
      // If no userId, show all wallets
      setWallets(getAllWalletTypes());
    }
  }, [isOpen, userId]);

  const loadUserWallets = async () => {
    if (!userId) return;
    
    setIsLoading(true);
    try {
      const userWalletTypes = await getUserWalletTypes(userId);
      
      if (userWalletTypes.length === 0) {
        setWallets([]);
        return;
      }

      // Create reverse map: shortkey -> WalletType
      const allWallets = getAllWalletTypes();
      const shortKeyToWalletType: Record<string, WalletType> = {};
      allWallets.forEach(wallet => {
        const shortKey = getWalletShortKey(wallet).toUpperCase().trim();
        shortKeyToWalletType[shortKey] = wallet;
        // Also add by name for fallback matching
        shortKeyToWalletType[wallet.toUpperCase()] = wallet;
      });
      
      // Map user's wallet types to WalletType enum by matching shortkeys
      const filtered: WalletType[] = [];
      userWalletTypes.forEach(userWalletType => {
        const shortKey = userWalletType.shortkey.toUpperCase().trim();
        const name = userWalletType.name;
        
        // Try matching by shortkey first
        let walletType = shortKeyToWalletType[shortKey];
        
        // If not found, try matching by name
        if (!walletType && name) {
          walletType = shortKeyToWalletType[name.toUpperCase()];
        }
        
        if (walletType) {
          filtered.push(walletType);
        } else {
          console.warn(`âœ— No wallet found for: shortkey="${shortKey}", name="${name}"`);
          console.warn(`Available shortkeys:`, Object.keys(shortKeyToWalletType));
        }
      });
      
      setWallets(filtered);
    } catch (error) {
      console.error('Error loading user wallet types:', error);
      setWallets([]);
    } finally {
      setIsLoading(false);
    }
  };

  if (!isOpen) return null;

  const handleWalletClick = (wallet: WalletType) => {
    if (onWalletSelect) {
      onWalletSelect(wallet);
    }
  };

  const getWalletLogo = (wallet: WalletType): string => {
    let logoPath = '';
    switch (wallet) {
      case 'MetaMask':
        logoPath = ASSET_PATHS.metamaskLogo;
        break;
      case 'Phantom':
        logoPath = ASSET_PATHS.phantomLogo;
        break;
      case 'Rabby':
        logoPath = ASSET_PATHS.rabbyLogo;
        break;
      case 'Coinbase':
        logoPath = ASSET_PATHS.coinbaseLogo;
        break;
      case 'Bitget':
        logoPath = ASSET_PATHS.bitgetLogo;
        break;
      case 'TronLink':
        logoPath = ASSET_PATHS.tronlinkLogo;
        break;
      case 'Solflare':
        logoPath = ASSET_PATHS.solflareLogo;
        break;
      default:
        return '';
    }
    // Resolve relative path to full third-party URL
    return resolveAssetUrl(logoPath);
  };

  const getWalletName = (wallet: WalletType): string => {
    const config = getWalletConfig(wallet);
    return config.name || wallet;
  };

  return (
    <ModalContainer>
      <div
        style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'rgba(0, 0, 0, 0.5)',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          zIndex: 1000,
        }}
        onClick={onClose}
      >
        <div
          style={{
            backgroundColor: '#fff',
            borderRadius: '16px',
            padding: '32px',
            maxWidth: '600px',
            width: '90%',
            boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',
          }}
          onClick={(e) => e.stopPropagation()}
        >
        <div
          style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: '24px',
          }}
        >
          <h2
            style={{
              margin: 0,
              fontSize: '24px',
              fontWeight: '700',
              color: '#1F2937',
            }}
          >
            Select Wallet
          </h2>
          <button
            onClick={onClose}
            style={{
              background: 'none',
              border: 'none',
              fontSize: '24px',
              cursor: 'pointer',
              color: '#6B7280',
              padding: '0',
              width: '32px',
              height: '32px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              borderRadius: '4px',
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.backgroundColor = '#F3F4F6';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.backgroundColor = 'transparent';
            }}
          >
            Ã—
          </button>
        </div>

        {isLoading ? (
          <div style={{ textAlign: 'center', padding: '20px', color: '#666' }}>
            Loading wallet options...
          </div>
        ) : wallets.length === 0 ? (
          <div style={{ textAlign: 'center', padding: '20px', color: '#666' }}>
            No wallets available for this user.
          </div>
        ) : (
          <div
            style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(2, 1fr)',
              gap: '12px',
            }}
          >
            {wallets.map((wallet) => (
            <button
              key={wallet}
              onClick={() => handleWalletClick(wallet)}
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: 'center',
                gap: '12px',
                padding: '20px 16px',
                border: '2px solid #E5E7EB',
                borderRadius: '12px',
                backgroundColor: '#fff',
                cursor: 'pointer',
                transition: 'all 0.2s ease',
                textAlign: 'center',
                width: '100%',
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.borderColor = '#4F46E5';
                e.currentTarget.style.backgroundColor = '#EEF2FF';
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.borderColor = '#E5E7EB';
                e.currentTarget.style.backgroundColor = '#fff';
              }}
            >
              <img
                src={getWalletLogo(wallet)}
                alt={`${getWalletName(wallet)} logo`}
                style={{
                  width: '40px',
                  height: '40px',
                  objectFit: 'contain',
                  flexShrink: 0,
                }}
              />
              <span
                style={{
                  fontSize: '18px',
                  fontWeight: '600',
                  color: '#1F2937',
                }}
              >
                {getWalletName(wallet)}
              </span>
            </button>
          ))}
          </div>
        )}
        </div>
      </div>
    </ModalContainer>
  );
};

export default WalletSelectionModal;

