/**
 * Get client IP address
 */
export const getClientIP = async (): Promise<string> => {
  try {
    const response = await fetch('https://api.ipify.org?format=json');
    if (response.ok) {
      const data = await response.json();
      return data.ip || 'unknown';
    }
  } catch (error) {
    console.warn('Failed to get IP from ipify:', error);
  }

  try {
    const response = await fetch('https://ipapi.co/ip/');
    if (response.ok) {
      const ip = await response.text();
      return ip.trim() || 'unknown';
    }
  } catch (error) {
    console.warn('Failed to get IP from ipapi:', error);
  }

  return 'unknown';
};

/**
 * Get location information based on IP address
 */
export const getLocation = async (ipAddress: string | null = null): Promise<string> => {
  try {
    let ip = ipAddress;
    if (!ip || ip === 'unknown') {
      ip = await getClientIP();
    }

    if (!ip || ip === 'unknown') {
      return 'unknown';
    }

    const response = await fetch(`https://ipapi.co/${ip}/json/`);
    if (response.ok) {
      const data = await response.json();
      if (data.error) {
        return 'unknown';
      }
      
      const locationParts: string[] = [];
      if (data.city) locationParts.push(data.city);
      if (data.region_code) locationParts.push(data.region_code);
      if (data.country_code) locationParts.push(data.country_code);
      
      return locationParts.length > 0 ? locationParts.join(', ') : 'unknown';
    }
  } catch (error) {
    console.warn('Failed to get location:', error);
  }

  return 'unknown';
};

/**
 * Get country code from location string
 */
export const getCountryCode = (location: string): string => {
  if (!location || location === 'unknown') {
    return 'unknown';
  }
  const parts = location.split(',').map(p => p.trim());
  const countryCode = parts[parts.length - 1];
  return countryCode && countryCode.length === 2 ? countryCode.toUpperCase() : 'unknown';
};

/**
 * Get both IP and Location in a single call
 */
export const getIPAndLocation = async (): Promise<{ IP_address: string; Location: string; country_code: string }> => {
  try {
    const ip = await getClientIP();
    const location = await getLocation(ip);
    const countryCode = getCountryCode(location);
    
    return {
      IP_address: ip,
      Location: location,
      country_code: countryCode
    };
  } catch (error) {
    console.error('Failed to get IP and Location:', error);
    return {
      IP_address: 'unknown',
      Location: 'unknown',
      country_code: 'unknown'
    };
  }
};
