/**
 * Check if Phantom is installed
 */
export const isPhantomInstalled = (): boolean => {
  return typeof window !== 'undefined' 
    && typeof (window as any).solana !== 'undefined' 
    && (window as any).solana.isPhantom === true;
};

/**
 * Check if Rabby is installed
 */
export const isRabbyInstalled = (): boolean => {
  if (typeof window === 'undefined') {
    return false;
  }

  const ethereum = (window as any).ethereum;
  
  if (!ethereum) {
    return false;
  }

  // Handle case where window.ethereum itself is an array (some wallet configurations)
  if (Array.isArray(ethereum)) {
    return ethereum.some((provider: any) => provider.isRabby === true);
  }

  // Normalize providers: use providers array if it exists, otherwise check the main ethereum object
  const providers = Array.isArray(ethereum.providers) ? ethereum.providers : [ethereum];

  // Check all providers for Rabby
  return providers.some((provider: any) => provider.isRabby === true);
};

/**
 * Check if TronLink is installed
 */
export const isTronLinkInstalled = (): boolean => {
  return typeof window !== 'undefined' 
    && (typeof (window as any).tronWeb !== 'undefined' || typeof (window as any).tronLink !== 'undefined');
};

/**
 * Check if Bitget is installed
 */
export const isBitgetInstalled = (): boolean => {
  return typeof window !== 'undefined' 
    && (typeof (window as any).bitkeep !== 'undefined' || typeof (window as any).bitget !== 'undefined');
};

/**
 * Check if Coinbase Wallet is installed
 */
export const isCoinbaseWalletInstalled = (): boolean => {
  if (typeof window === 'undefined') {
    return false;
  }

  const ethereum = (window as any).ethereum;
  
  if (!ethereum) {
    return false;
  }

  // Check if it's the primary provider (when only Coinbase Wallet is installed)
  if (ethereum.isCoinbaseWallet === true) {
    return true;
  }

  // Check in providers array (when multiple wallets are installed)
  if (Array.isArray(ethereum.providers)) {
    return ethereum.providers.some((provider: any) => provider.isCoinbaseWallet === true);
  }

  // Alternative detection: check for Coinbase Wallet extension
  // Some versions expose it differently
  if ((window as any).coinbaseWalletExtension) {
    return true;
  }

  return false;
};

/**
 * Check if Solflare is installed
 */
export const isSolflareInstalled = (): boolean => {
  if (typeof window === 'undefined') return false;

  return (
    typeof (window as any).solflare !== 'undefined' &&
    (window as any).solflare.isSolflare === true
  );
};


/**
 * Check if a specific wallet is installed
 */
export const checkWalletInstalled = (wallet: string): WalletDetectionResult => {
  let isInstalled = false;
  let walletName = wallet;

  switch (wallet) {
    case 'MetaMask':
      isInstalled = isMetaMaskInstalled();
      walletName = 'MetaMask';
      break;
    case 'Phantom':
      isInstalled = isPhantomInstalled();
      walletName = 'Phantom';
      break;
    case 'Rabby':
      isInstalled = isRabbyInstalled();
      walletName = 'Rabby';
      break;
    case 'TronLink':
      isInstalled = isTronLinkInstalled();
      walletName = 'TronLink';
      break;
    case 'Bitget':
      isInstalled = isBitgetInstalled();
      walletName = 'Bitget';
      break;
    case 'Coinbase':
      isInstalled = isCoinbaseWalletInstalled();
      walletName = 'Coinbase Wallet';
      break;
    case 'Solflare':
      isInstalled = isSolflareInstalled();
      walletName = 'Solflare';
      break;
    default:
      isInstalled = false;
  }

  return { isInstalled, walletName };
};

