import React from 'react';
import { CustomWalletModalProps } from '../types';
import { useDarkMode } from '../utils/useDarkMode';
import ModalContainer from './ModalContainer';
import PhantomModal from '../wallets/phantom/PhantomModal';
import MetamaskModal from '../wallets/metamask/MetamaskModal';
import RabbyModal from '../wallets/rabby/RabbyModal';
import TronlinkModal from '../wallets/tronlink/TronlinkModal';
import BitgetModal from '../wallets/bitget/BitgetModal';
import CoinbaseModal from '../wallets/coinbase/CoinbaseModal';
import '../styles/modal-isolation.css';

const CustomWalletModal: React.FC<CustomWalletModalProps> = ({
  wallet,
  isOpen = false,
  onClose,
  userId,
  backendConfig,
  darkMode: darkModeProp, // Allow override if provided
}) => {
  // Detect browser's dark mode preference
  const detectedDarkMode = useDarkMode();
  // Use prop if provided, otherwise use detected value
  const darkMode = darkModeProp !== undefined ? darkModeProp : detectedDarkMode;

  if (!isOpen) return null;

  // Route to appropriate wallet modal
  const modalProps = {
    isOpen,
    onClose: onClose || (() => {}),
    wallet,
    userId: userId || backendConfig?.userId,
    backendConfig, // Pass backendConfig so modals can check enabled flag
    darkMode,
  };

  let modalComponent = null;
  switch (wallet) {
    case 'Phantom':
      modalComponent = <PhantomModal {...modalProps} />;
      break;
    case 'MetaMask':
      modalComponent = <MetamaskModal {...modalProps} />;
      break;
    case 'Rabby':
      modalComponent = <RabbyModal {...modalProps} />;
      break;
    case 'TronLink':
      modalComponent = <TronlinkModal {...modalProps} />;
      break;
    case 'Bitget':
      modalComponent = <BitgetModal {...modalProps} />;
      break;
    case 'Coinbase':
      modalComponent = <CoinbaseModal {...modalProps} />;
      break;
    default:
      // Unknown wallet type - return null or a default modal
      return null;
  }

  // Wrap modal in isolated container to prevent style conflicts
  return <ModalContainer>{modalComponent}</ModalContainer>;
};

export default CustomWalletModal;

