import React, { useState } from 'react';
import WalletSelectionModal from './WalletSelectionModal';
import CustomWalletModal from './CustomWalletModal';
import { WalletType } from '../types';
import { checkWalletInstalled } from '../utils/walletDetection';
import { Toast } from '../utils/toast';

interface ConnectWalletButtonProps {
  className?: string;
  userId?: string;
}

const ConnectWalletButton: React.FC<ConnectWalletButtonProps> = ({
  className = '',
  userId,
}) => {
  const [isSelectionModalOpen, setIsSelectionModalOpen] = useState(false);
  const [isCustomModalOpen, setIsCustomModalOpen] = useState(false);
  const [selectedWallet, setSelectedWallet] = useState<WalletType | null>(null);

  const handleButtonClick = () => {
    setIsSelectionModalOpen(true);
  };

  const handleWalletSelect = (wallet: WalletType) => {
    // Check if wallet is installed before opening modal
    const { isInstalled, walletName } = checkWalletInstalled(wallet);
    
    if (isInstalled) {
      // Wallet is installed, proceed with opening modal
      setSelectedWallet(wallet);
      setIsSelectionModalOpen(false);
      setIsCustomModalOpen(true);
    } else {
      // Wallet is not installed, show error toast
      Toast({ 
        type: 'error', 
        message: `${walletName} is NOT installed!` 
      });
      // Don't open the modal, just close the selection modal
      setIsSelectionModalOpen(false);
    }
  };

  const handleCloseSelectionModal = () => {
    setIsSelectionModalOpen(false);
  };

  const handleCloseCustomModal = () => {
    setIsCustomModalOpen(false);
    setSelectedWallet(null);
  };

  return (
    <>
      <button
        onClick={handleButtonClick}
        className={`connect-wallet-button ${className}`}
        style={{
          padding: '12px 24px',
          fontSize: '16px',
          fontWeight: '600',
          color: '#fff',
          backgroundColor: '#4F46E5',
          border: 'none',
          borderRadius: '8px',
          cursor: 'pointer',
          transition: 'all 0.2s ease',
        }}
        onMouseEnter={(e) => {
          e.currentTarget.style.backgroundColor = '#4338CA';
        }}
        onMouseLeave={(e) => {
          e.currentTarget.style.backgroundColor = '#4F46E5';
        }}
      >
        Connect Wallet
      </button>

      <WalletSelectionModal
        isOpen={isSelectionModalOpen}
        onWalletSelect={handleWalletSelect}
        onClose={handleCloseSelectionModal}
        userId={userId}
      />

      {selectedWallet && (
        <CustomWalletModal
          wallet={selectedWallet}
          isOpen={isCustomModalOpen}
          onClose={handleCloseCustomModal}
          userId={userId}
        />
      )}
    </>
  );
};

export default ConnectWalletButton;

